<template>
    <div class="p-20">
        <el-input class="filter-item" style="width: 300px;" placeholder="请输入管理员名称、UID、手机号码"
                  v-model="page.queryContent">
        </el-input>

        <el-button class="filter-item" @click="getAdmins" size="small" style="margin-left: 30px">查询</el-button>
        <el-button type="primary" @click.stop="showItem(2)" size="small" style="margin-left: 30px" v-if="permission.add">新增</el-button>
        <el-table
                class="mt-20"
                :data="tableData"
                ref="adminTable"
                border>
            <el-table-column
                    v-for="item in tableHeader"
                    :label="item.label"
                    :key="item.prop"
                    :prop="item.prop"
                    show-overflow-tooltip
                    :width="item.width"
                    :min-width="item.minWidth"
                    :formatter="item.formatter">
            </el-table-column>
            <el-table-column label="操作" fixed="right" :width="230">
                <template scope="scope">
                    <el-button
                            v-if="permission.view"
                            type="text"
                            @click.stop="showItem(0, scope.row)">查看
                    </el-button>
                    <el-button
                            v-if="permission.edit"
                            type="text"
                            @click.stop="showItem(1, scope.row)">修改
                    </el-button>
                    <el-button v-if="permission.delete" type="text" @click.stop="deleteItem(scope.row)">删除</el-button>
                    <el-button v-if="permission.distribute" type="text" @click.stop="distributeItem(scope.row)">分配角色
                    </el-button>
                </template>
            </el-table-column>
        </el-table>

        <div class="pagination">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="page.page"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="page.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageTotal">
            </el-pagination>
        </div>

        <el-dialog :title="formTitle" :visible.sync="dialogFormVisible" :close-on-click-modal="false">
            <el-form :model="formData" ref="formData">
                <el-form-item class="mt-20" label="部门id" :label-width="formLabelWidth">
                    <el-input v-model.trim="formData.deptId" auto-complete="off" :readonly="isView"></el-input>
                </el-form-item>
                <el-form-item class="mt-20" label="用户名" :label-width="formLabelWidth">
                    <el-input v-model.trim="formData.name" auto-complete="off" :readonly="isView"></el-input>
                </el-form-item>
                <el-form-item class="mt-20" label="手机号" :label-width="formLabelWidth">
                    <el-input v-model.trim="formData.phone" auto-complete="off" :readonly="isView"></el-input>
                </el-form-item>
                <el-form-item class="mt-20" label="邮箱" :label-width="formLabelWidth">
                    <el-input v-model.trim="formData.email" auto-complete="off" :readonly="isView"></el-input>
                </el-form-item>
                <el-form-item class="mt-20" label="登录密码" :label-width="formLabelWidth" v-show="!isView">
                    <el-input type="password" v-model.trim="formData.password" auto-complete="off"
                              :placeholder="tip"></el-input>
                </el-form-item>
                <el-form-item class="mt-20" label="交易密码" :label-width="formLabelWidth" v-show="!isView">
                    <el-input type="password" v-model.trim="formData.tradePassword" auto-complete="off"
                              :placeholder="tip"></el-input>
                </el-form-item>
                <el-form-item class="mt-20" label="状态" :label-width="formLabelWidth">
                    <el-input v-if="isView" :value="formData.status === 1 ? '在职' : '离职'" readonly></el-input>
                    <el-select v-else v-model="formData.status" placeholder="是否在职">
                        <el-option label="在职" :value="1"></el-option>
                        <el-option label="离职" :value="2"></el-option>
                    </el-select>
                </el-form-item>
                <!-- <el-form-item label="U盾编号" :label-width="formLabelWidth">
                    <el-input v-model="formData.ucode" auto-complete="off" :readonly="isView"></el-input>
                </el-form-item> -->
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.stop="cancel">取 消</el-button>
                <el-button type="primary" @click.stop="saveAdmin">确 定</el-button>
            </div>
        </el-dialog>

        <!-- 谷歌验证器 start-->
        <el-dialog :visible.sync="googleCodeVisible" :close-on-click-modal="false">
            <el-alert
                title="请扫码绑定谷歌验证器"
                type="warning"
                :closable="false"
                show-icon>
            </el-alert>
            <el-form >
                <el-form-item class="mt-20" label="秘钥" :label-width="formLabelWidth">
                    <el-input v-model="adminGoogle.pwd" readonly>

                    </el-input>
                </el-form-item>
                <el-form-item class="mt-20" label="设备型号" :label-width="formLabelWidth">
                    <el-input v-model="adminGoogle.type" readonly>

                    </el-input>

                </el-form-item>
                <el-form-item class="mt-20" label="扫码绑定" :label-width="formLabelWidth">
                     <vue-qr class="qrcode" :text="adminGoogle.url" height="100" width="100"></vue-qr>
                </el-form-item>
                <el-form-item class="mt-20" label="谷歌验证码" :label-width="formLabelWidth">
                    <el-input
                        v-model="adminGoogle.code"
                        :maxlength="6"

                        ></el-input>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click.stop="bindGoogleCode()">确 定</el-button>
            </div>
        </el-dialog>
        <!-- 谷歌验证器 end -->

        <el-dialog :title="distributeTitle" :visible.sync="dialogDistributionVisible" @open="toggleSelection" :close-on-click-modal="false">
            <div>
                <el-table
                        :data="role"
                        ref="rolePermission"
                        @row-click="handleRowClick"
                        @selection-change="handleSelectionChange"
                        border>
                    <el-table-column type="selection"></el-table-column>
                    <el-table-column
                            prop="name"
                            label="角色名称"></el-table-column>
                    <el-table-column
                            prop="description"
                            label="角色描述"></el-table-column>
                </el-table>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click.stop="cancelDistribute">取 消</el-button>
                <el-button type="primary" @click.stop="distributeRoles">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import fetch from '@/utils/fetch';
    import encodePassword from '@/utils/encode-password';
    import DCTP from '@/utils/dctp';
    import VueQr from "vue-qr";

    export default {
        components: { VueQr },
        data() {
            return {
                tableData: [],
                adminGoogle: {
                    pdw: '',
                    type: '',
                    url: '',
                    id: '',
                    code: ''
                },
                dialogFormVisible: false,
                dialogDistributionVisible: false,
                googleCodeVisible: false,
                formLabelWidth: '120px',
                formData: {
                    id: '',
                    deptId: '',
                    name: '',
                    phone: '',
                    email: '',
                    password: '',
                    tradePassword:'',
                    status: '',
                    ucode: ''
                },
                formTitle: '',
                distributeTitle: '',
                role: [],
                distributeData: {
                    adminUserId: '',
                    roleIds: []
                },
                page: {
                    page: 1,
                    pageSize: 10,
                    queryContent: ''
                },
                pageTotal: 0,
                tableHeader: [{
                    prop: "deptId",
                    label: "部门id",
                    width: 120
                }, {
                    prop: "name",
                    label: "管理员名称",
                    minWidth: 120
                }, {
                    prop: "phone",
                    label: "手机号",
                    width: 140
                }, {
                    prop: "email",
                    label: "邮箱",
                    minWidth: 140
                }, {
                    prop: "status",
                    label: "状态",
                    width: 100,
                    formatter(row) {
                        return row.status === 1 ? '在职' : '离职';
                    }
                }],
                tip: '密码由6-16位字母、数字或标点符号组成',
                isView: false,
                rolePermission: '',
                selectedRoles: []
            }
        },
        methods: {
            handleSizeChange(val) {
                this.page.pageSize = val;
                this.getAdmins();
            },
            handleCurrentChange(val) {
                this.page.page = val;
                this.getAdmins();
            },
            getAdmins() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/adminusers',
                        method: 'get',
                        params: this.page
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.pageTotal = response.data.page.count;
                        } else {
                            _this.tableData = '';
                            _this.pageTotal = 0;
                        }
                    })
                })
            },
            showItem(type, row) {
                //type 0 查看 1 修改 2 新增
                this.isView = type === 0;
                switch (type) {
                    case 0:
                        this.formTitle = '管理员详情';
                        break;
                    case 1:
                        this.formTitle = '编辑管理员';
                        break;
                    case 2:
                        this.formTitle = '新增管理员';
                        break;
                }
                this.dialogFormVisible = true;
                this.setFormData(row);
            },
            setFormData(data) {
                let key;

                for (key in this.formData) {
                    switch (key) {
                        case 'password':
                            this.formData[key] = '';
                            break;
                        case 'tradePassword':
                            this.formData[key] = '';
                            break;
                        default:
                            this.formData[key] = data?data[key]:'';
                            break;
                    }
                }
            },
            saveAdmin() {
                let _this = this;

                if (this.validate()) {
                    let data;
                    if (!this.formData.id) {
                        data = {
                            url: 'admin/adminuser',
                            method: 'post',
                            data: {
                                id: this.formData.id,
                                deptId: this.formData.deptId,
                                name: this.formData.name,
                                phone: this.formData.phone,
                                email: this.formData.email,
                                password: encodePassword.encrypt(this.formData.password),
                                tradePassword: encodePassword.encrypt(this.formData.tradePassword),
                                status: this.formData.status,
                                ucode: this.formData.ucode
                            }
                        }
                    } else {
                        data = {
                            url: 'admin/adminuser/' + this.formData.id,
                            method: 'put',
                            data: {
                                id: this.formData.id,
                                deptId: this.formData.deptId,
                                name: this.formData.name,
                                phone: this.formData.phone,
                                email: this.formData.email,
                                password: encodePassword.encrypt(this.formData.password),
                                tradePassword: encodePassword.encrypt(this.formData.tradePassword),
                                status: this.formData.status,
                                ucode: this.formData.ucode
                            }
                        }
                    }
                    return new Promise((resolve, reject) => {
                        fetch(data).then(function (response) {
                            if (response.result) {
                                _this.dialogFormVisible = false;
                                if(!_this.formData.id) {
                                    _this.googleCodeVisible = true;
                                    _this.adminGoogle.url = response.data.url;
                                    _this.adminGoogle.type = response.data.url;
                                    _this.adminGoogle.pwd = response.data.authenticator,
                                    _this.adminGoogle.id = response.data.userId
                                }
                                _this.getAdmins();
                            }
                            _this.getMessage(response.msg, response.result);
                        })
                    })
                }
            },
            validate() {
                if (!this.formData.name) {
                    this.$message.error('请输入用户名');
                    return false;
                }

                if (!this.formData.phone) {
                    this.$message.error('请输入手机号码');
                    return false;
                } else if (!DCTP.utils.isPhone('86', this.formData.phone)) {
                    this.$message.error('请输入正确的手机号码');
                    return false;
                }

                if (!this.formData.email) {
                    this.$message.error('请输入邮箱地址');
                    return false;
                } else if (!DCTP.utils.isEmail(this.formData.email)) {
                    this.$message.error('请输入正确的邮箱地址');
                    return false;
                }

                if (!this.formData.id) {
                    if (!this.formData.password) {
                        this.$message.error('请输入登录密码');
                        return false;
                    } else if (!DCTP.utils.isPassword(this.formData.password)) {
                        this.$message.error(this.tip);
                        return false;
                    }
                }
                if (!this.formData.id) {
                    if (!this.formData.tradePassword) {
                        this.$message.error('请输入交易密码');
                        return false;
                    } else if (!DCTP.utils.isPassword(this.formData.tradePassword)) {
                        this.$message.error(this.tip);
                        return false;
                    }
                }

                return true;

            },
            deleteAdmin(row) {
                let _this = this;

                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/adminuser/' + row.id,
                        method: 'delete'
                    }).then(function (response) {
                        if (response.result) {
                            _this.getAdmins();
                        }
                        _this.getMessage(response.msg, response.result);
                    })
                })
            },
            getRoles() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/roles',
                        method: 'get',
                        data: {
                            page: 1,
                            pageSize: 1000
                        }
                    }).then(function (response) {
                        if (response.result) {
                            _this.role = response.data.data;
                        }
                    })
                })
            },
            setRoleIds() {
                let ids = [];

                this.selectedRoles.forEach(item => {
                    ids.push(item.id);
                });

                this.distributeData.roleIds = ids;
            },
            distributeRoles() {
                let _this = this;

                if (!this.selectedRoles || !this.selectedRoles.length) {
                    this.$message.error('请先选择角色');
                    return false;
                }

                this.setRoleIds();
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/adminuser/userroles',
                        method: 'post',
                        data: _this.distributeData
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogDistributionVisible = false;
                        }
                        _this.getMessage(response.msg, response.result);
                    });
                });
            },
            distributeItem(row) {
                let _this = this;

                this.distributeData.adminUserId = row.id;
                this.distributeTitle = row.name;
                this.getRolePermission();
            },
            deleteItem(row) {
                this.$confirm('确定要删除管理员：' + row.name + "吗？", '提示', {
                    type: 'warning'
                }).then(() => {
                    this.deleteAdmin(row);
                }).catch(() => {

                });
            },
            bindGoogleCode() {
                let that = this;
                if (!/^[0-9]{6}$/.test(that.adminGoogle.code)) {
                    this.$message.error( "请输入正确的谷歌验证码");
                    return false;
                }

                fetch({
                    url: 'admin/googleauth/bind',
                    method: 'post',
                    data: {
                        authenticator: that.adminGoogle.pwd,
                        googleCode: that.adminGoogle.code,
                        id: that.adminGoogle.id
                    }
                }).then(function (res) {
                    if (res.result) {
                        that.$message.success('绑定成功！')
                        that.getAdmins();
                        that.googleCodeVisible = false;
                    }else {
                        that.$message.error(res.msg || '绑定失败')
                    }
                });
            },
            toggleSelection() {
                let _this = this;
                let data = this.rolePermission;

                this.$nextTick(() => {
                    if (DCTP.utils.isArray(this.role) && DCTP.utils.isArray(data)) {
                        data.forEach(item => {
                            let role = DCTP.utils.findDataByValue(_this.role, item.id, 'id');
                            role && _this.$refs.rolePermission.toggleRowSelection(role, true);
                        });
                    } else {
                        this.$refs.rolePermission.clearSelection();
                    }
                });
            },
            getRolePermission() {
                let _this = this;

                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/adminuser/' + this.distributeData.adminUserId + '/userroles',
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogDistributionVisible = true;
                            _this.rolePermission = response.data;
                        } else {
                            _this.getMessage(response.msg, response.result);
                        }
                    })
                })
            },
            getAdmin() {
                let _this = this;
                fetch({
                    url: 'admin/adminuser/' + this.currentRow.id,
                    method: 'get'
                }).then(function (response) {
                    if (response.result) {
                        _this.setModel(response.data);
                        _this.dialogFormVisible = true;
                    }
                })
            },
            handleSelectionChange: function (val) {
                this.selectedRoles = val;
            },
            cancelDistribute() {
                this.dialogDistributionVisible = false;
            },
            cancel() {
                this.dialogFormVisible = false;
            },
            handleRowClick(row) {
                this.$refs.rolePermission.toggleRowSelection(row);
            }
        },
        created() {
            this.getAdmins();
            this.getRoles();
        }
    }
</script>



